import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'

import AlertMessage from './views/alertMessage'

import Home from './views/home'
import Login from './views/login'
import Invite from './views/invite'
import Academias from './views/academias'
import Lista from './views/cadastro/waitList'

class Main extends Component {
  render() {
    return (
      <main id="top">
        <AlertMessage title="Olá," description=" Estamos construindo nossas aplicações, mas você já pode fazer o seu pré-cadastro."/>
         <Switch>
          <Route onUpdate={window.scrollTo(0, 0)} exact path='/' component={Home}/>
          <Route onUpdate={window.scrollTo(0, 0)} exact path='/login' component={Login}/>
          <Route onUpdate={window.scrollTo(0, 0)} exact path='/lista' component={Lista}/>
          <Route onUpdate={window.scrollTo(0, 0)} exact path='/academias' component={Academias}/>
          <Route onUpdate={window.scrollTo(0, 0)} exact path='/pre-cadastro' component={Invite}/>
        </Switch>
      </main>
    );
  }
}
export default Main


