import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class Navigation extends Component {
  render() {
    return (
      <nav role="custom-dropdown">
        <div className="group-buttons">
          <Link className="button button-action" to="/pre-cadastro"><span className="text-a">Pré-cadastro</span><span className="text-b">Quero ser Ufit!</span></Link>
        </div>
        <input type="checkbox" id="button" />
        <label htmlFor="button"/>
        <ul>
          <li><Link to="/" className="wide">Home</Link></li>
          <li><Link to="/#slide" className="wide">Como Funciona</Link></li>
          <li><Link to="/academias" className="wide">Para Academias</Link></li>
        </ul>
      </nav>
    );
  }
}

export default Navigation;


