import React, { Component } from 'react';
import { Link } from 'react-router-dom'

class Links extends Component {
  render() {
    return (
      <ul>
        <li><Link to="/#slide">Como Utilizar?</Link></li>
        <li><a id="open-popup">Pré-cadastro</a></li>
      </ul>
    );
  }
}

export default Links;


